
.container {
    height: 100vh;
    padding: 5rem;
    box-sizing: border-box;
  }
  
  /* Animation of Image */

  .vector-graphics {
    filter: drop-shadow(0 40px 10px rgba(0, 0, 0, 0.3));
    animation: vectorAnimation 1s infinite ease-in-out alternate;
  }
  
  @keyframes vectorAnimation {
    to {
      transform: translateY(-10px);
    }
  }
  
  /* SVG i.e. Brand Icons styling */

  .brandsBanner > svg {
    color: white;
    font-size: 3rem;
    transition: color 0.5s;
  }
  .brandsBanner > svg:hover {
    color: rgb(255, 221, 0);
  }
  
  /* Video Section Styling */

  .container2 {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container2 video {
    border: 1px solid rgba(0, 0, 0, 0.18);
    width: 60%;
    border-radius: 5px;
    outline: none;
  }
  
  /* Responsiveness of Video Section */
  
  @media screen and (max-width: 600px) {
    .container2 video {
      width: 100%;
    }
  }
  

  /* From Course Section */
  
  .course {
    width: 250px;
    margin-bottom: 1rem;
    transition: all 0.5s;
  }
  
  .course:hover {
    transform: translateY(-10px);
  }
  